<template>
    <div class="about-related">
        <navigate title="关于" position="absolute"/>
        <div class="scroll-wrapper">
            <div class="related-content">
                <div class="logo paper m-25">
                    <img :src="logo">
                </div>
                <h1 class="title">广东航道通</h1>
                <div class="version">版本: {{version}}</div>
                <div class="abstract">
                    <p>为提升广东航道公共服务水平，广东省航道事务中心开发了“广东航道通”APP，现上线测试。</p>
                    <p>
                        该APP采用智能手机客户端的方式，提供在线电子航道图、航标、桥梁通航尺度、水位等助航信息，为社会公众提供更加直观、高效的航道信息展示和移动互联的助航信息服务。测试阶段开通航线、助航、资讯、动态圈等栏目。欢迎参与测试的用户向我们反馈使用意见和建议。</p>
                </div>
                <router-link class="db p-15 tc color-primary" :to="{name: 'about-feedback'}">
                    欢迎对广东航道通客户端提出宝贵意见和建议
                </router-link>
                <div class="APP-Information mt-20 p-15 color-primary" @click="agree = true">
                    《广东航道通移动客户端用户使用协议》
                </div>
            </div>
        </div>


        <!-- 协议 -->
        <mu-dialog title="广东航道通移动客户端用户使用协议" width="500px" max-width="86%"
                   :open.sync="agree"
                   scrollable
                   lock-scroll>
            <service-agreement/>
        </mu-dialog>
        <!-- 协议 -->

    </div>
</template>

<script>
    import Navigate from "../../../base/navigate/navigate"
    import ServiceAgreement from "../../../components/agreement/service-agreement"

    export default {
        name: "aboutApp",
        data() {
            return {
                logo: "static/image/logo.png",
                panel: "",
                version: '0.0.0',
                agree: false
            }
        },
        created() {
            window.$cordova_api.app.version().then(res => {
                this.version = res

                console.log(`%c当前APP版本为 ${res}`, 'color: #673ab7')
            })
        },
        methods: {
            toggle(panel) {
                this.panel = panel === this.panel ? "" : panel;
            }
        },
        components: {
            Navigate,
            ServiceAgreement
        }
    };
</script>

<style lang="stylus">
    @import '../../../common/stylus/variable.styl';

    .about-related
        padding-top 46px

        .logo
            width 95px
            height 95px
            border-radius 13px
            box-shadow 0 0 15px -5px rgba(0, 0, 0, .33)

            > img
                display block
                padding 15px
                width 100%
                height 100%

        .related-content
            display flex
            flex-direction column
            align-items center
            min-height 100%

            h1.title
                margin-bottom 10px
                font-size 17px
                font-weight 500

            .version
                margin-bottom 5px
                font-size 12px
                opacity 0.7

            .abstract
                padding 0 27px
                line-height 30px
                font-size 14px
                p
                    text-indent 2em
                    opacity 0.7
                    text-align justify

            .APP-Information
                justify-self flex-end
</style>



